import Cell from './cell'
export default {
	components: {
		Cell
	},
	props: {
		/**
		 * data要传入的饿数据数组
		 */
		data: {
			type: Array,
			default () {
				return []
			}
		},
		columns: {
			type: Array,
			default () {
				return []
			}
		},
		border: {
			type: Boolean,
			default: false
		},
		height: {
			type: [Number, String],
			default: 0
		},
		headbBackground: {
			type: String,
			default: ""
		},
		noDataText: {
			type: String,
			default: "暂无数据"
		}

	},
	data: () => {
		return {
			allCheck: false
		}
	},
	methods: {
		/** 
		 * 	-- 单击某一行时触发
		 * @param {Object} item为当前行的数据
		 * @param {Object} index为当前行的下标
		 */
		clickRow(row, index) {
			let params = {
				row,
				index
			}
			this.$emit('on-row-click', params)
		},
		/**
		 * 	--	 点击全选
		 */
		checkHeader() {
			this.allCheck = !this.allCheck;
			this.data.forEach(it => {
				it._isChecked = this.allCheck;
			})
			this.$emit('on-select-all', {
				allChecked: this.data.filter(item => item._isChecked)
			})
		},
		/**
		 * 	--	点击单选
		 */
		checkItemCheckbox(item) {
			item._isChecked = !item._isChecked;
			this.data.forEach(it => {
				if(!it._isChecked){
					this.allCheck = false;
				}
			})
			let ary = this.data.filter(item => item._isChecked); //已选择的项
			if(ary.length == this.data.length){
				this.allCheck = true
			}
			this.$emit('on-select', {
				allChecked: ary,
				checked: item
			})
		}
	},
	created() {
		this.columns.forEach(it => {
			it.type = it.type || 'html';
			if (it.render) {
				it.type = "render"
			}
		})
		this.data.forEach(it => {
			it._isChecked = false;
		})
	},
	mounted() {

	},
	computed: {

	}

}
